import React from "react";

const BlogPost = ({ src, title, date, desc }) => {
  return (
    <div className="blog-post-container">
      <div className="left">
        <div className="img-container">
          <img src={src} />
        </div>
      </div>
      <div className="right">
        <div className="title-date">
          {title} <span>- {date}</span>
        </div>
        <div className="desc">
         {desc}
        </div>
        <div className="come-soon">Comming Soon</div>
        {/* <div className="btn-container">Read More</div> */}
      </div>
    </div>
  );
};

export default BlogPost;
