import React from "react";
import Layout from "../layout/Layout";
import { RiArrowUpSLine } from "react-icons/ri";
import { Row, Col, BackTop } from "antd";
import HomeSection4 from "../components/homesections/HomeSection4";
import HomeSection7 from "../components/homesections/HomeSection7";
import ServiceSection1 from "../components/servicesections/ServiceSection1";
import ServiceSection2 from "../components/servicesections/ServiceSection2";

const ServicePage = () => {
  return (
    <Layout>
      <div className="service-page-container">
        <Row className="sec-1">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <ServiceSection1 />
          </Col>
        </Row>
        <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <ServiceSection2 />
          </Col>
        </Row>
        <Row className="sec-3">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <HomeSection4 />
          </Col>
        </Row>
        <Row className="sec-4">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <HomeSection7 />
          </Col>
        </Row>
        <BackTop>
          <RiArrowUpSLine />
        </BackTop>
      </div>
    </Layout>
  );
};

export default ServicePage;
