import { BackTop, Col, Row } from "antd";
import React from "react";
import { RiArrowUpSLine } from "react-icons/ri";
import ContactSection1 from "../components/contactsections/ContactSection1";
import ContactSection2 from "../components/contactsections/ContactSection2";
import Test from "../components/test/Test";
import Layout from "../layout/Layout";

const Contact = () => {
  return (
    <Layout>
      <div className="contact-container">
        <Row className="sec-1">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
              <ContactSection1/>
          </Col>
        </Row>
        <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
              <ContactSection2/>
          </Col>
        </Row>
        {/* <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
              <Test/>
          </Col>
        </Row> */}
        <BackTop>
          <RiArrowUpSLine />
        </BackTop>
      </div>
    </Layout>
  );
};

export default Contact;
