import React from "react";
import { Row, Col , BackTop} from "antd";
import { RiArrowUpSLine } from "react-icons/ri";
import Layout from "../layout/Layout";
import ProjectDetailSection1 from "../components/projectdetailsections/ProjectDetailSection1";
import HomeSection4 from "../components/homesections/HomeSection4"
import ProjectDetailSection2 from "../components/projectdetailsections/ProjectDetailSection2";

const ProjectDetail = () => {
  return (
    <Layout>
      <div className="project-container">
        <Row className="sec-1">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <ProjectDetailSection1/>
          </Col>
        </Row>
        <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
          <ProjectDetailSection2/>
          </Col>
        </Row>
        <Row className="sec-3">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
              <HomeSection4/>
          </Col>
        </Row>
        <BackTop>
          <RiArrowUpSLine />
        </BackTop>
      </div>
    </Layout>
  );
};

export default ProjectDetail;
