import React, { useEffect, useState } from "react";
import { Row, Col, Carousel } from "antd";
import Service from "../service card/Service";
import { ImStack } from "react-icons/im";
import { FaHammer, FaPenFancy } from "react-icons/fa";

const HomeSection2 = () => {
  const serviceData = [
    {
      icon: <FaHammer />,
      title: "Web Development",
      subServices: [
        "HTML/CSS",
        "JavaScript",
        "Animation",
        "WordPress",
        "React",
      ],
    },
    {
      icon: <ImStack />,
      title: "Mobile App Development",
      subServices: [
        "React Native",
        "IOS",
        "Flutter",
        "Mobile Angular UI",
        "Mobile App Design",
      ],
    },
    {
      icon: <FaPenFancy />,
      title: "Illustration",
      subServices: [
        "Character Design",
        "Icon Set",
        "Illustration Guide",
        "Illustration Set",
        "Motion Graphic",
      ],
    },
    {
      icon: <ImStack />,
      title: "UI/UX Design",
      subServices: [
        "Landing Pages",
        "User Flow",
        "Wireframing",
        "Prototyping",
        "Mobile App Design",
      ],
    },
  ];

  const [data, setData] = useState(null);
  const [data2, setData2] = useState(null);

  useEffect(() => {
    setData(serviceData);
  }, []);

  useEffect(() => {
    let tempArr = [];
    let prev = 0;
    let next = 2;
    for (let i = 0; i <= data?.length - 1; i++) {
      if (next <= data?.length) {
        tempArr = [...tempArr, data?.slice(prev, next)];
      }
      prev = prev + 2;
      next = next + 2;
    }
    console.log(tempArr);
    setData2(tempArr);
  }, [data]);

  useEffect(() => {
    console.log(data2);
  }, [data2]);

  return (
    <div className="home-section-2-container">
      <div className="sec-1">
        <div className="top">
          <div className="line-container">
            <div className="line"></div>
            <div className="line"></div>
          </div>
          <div className="text-container">My Services</div>
        </div>
        <div className="title">Service Provide For My Clients.</div>
      </div>
      <div className="sec-2">
        <Carousel className="service-carousel">
          {data2?.map((itemArr) => {
            console.log(itemArr);
            return (
              <div className="service-wrappers-container">
                {itemArr?.map((item) => {
                  return (
                    <div className="service-wrapper">
                      <Service
                        icon={item?.icon}
                        title={item?.title}
                        subServices={item?.subServices}
                      />
                    </div>
                  );
                })}
              </div>
            );
          })}
        </Carousel>
      </div>
    </div>
  );
};

export default HomeSection2;
