import React from "react";
import { Dropdown, Menu } from "antd";
import Logo from "../../assets/Logo.png";
import { Link } from "react-router-dom";

const service = (
  <Menu
    items={[
      {
        key: "1",
        label: <Link to={"/service"}>Services</Link>,
      },
      {
        key: "2",
        label: <Link to={"/service-details"}>Service Details</Link>,
      },
    ]}
  />
);

const page = (
  <Menu
    items={[
      {
        key: "2",
        label: <Link to={"/projects"}>Projects</Link>,
      },
    ]}
  />
);

const Navbar = () => {
  return (
    <div className="nav-main-container">
      <div className="logo-container">
        {/* <img src={Logo} /> */}
        Usama Ansari
      </div>
      <ul>
        <li>
          <img className="non-drop-img" src="https://htmldemo.net/lendex/lendex/assets/images/icon/menu-hover-shape.png" />
          <Link to={"/"}>Home</Link>
        </li>
        <li>
          <Dropdown overlay={service}>
            <div className="drop-down-custom-inner">
              <img className="non-drop-img" src="https://htmldemo.net/lendex/lendex/assets/images/icon/menu-hover-shape.png" />
              <Link to={"/service"}>Service</Link>
            </div>
          </Dropdown>
        </li>
        {/* <li>
          <Dropdown overlay={service}>
            <a>Blog</a>
          </Dropdown>
        </li> */}
        <li>
          <Dropdown overlay={page}>
            <div className="drop-down-custom-inner">
              <img className="non-drop-img" src="https://htmldemo.net/lendex/lendex/assets/images/icon/menu-hover-shape.png" />
              <Link to={"/projects"}>Projects</Link>
            </div>
          </Dropdown>
        </li>
        <li>
          <img className="non-drop-img" src="https://htmldemo.net/lendex/lendex/assets/images/icon/menu-hover-shape.png" />
          <Link to={"/contact"}>Contact</Link>
        </li>
      </ul>
    </div>
  );
};

export default Navbar;
