import React, { useState } from "react";
import { Row, Col } from "antd";
import { BsFillPersonFill, BsCalendar3 } from "react-icons/bs";
import { FiSettings } from "react-icons/fi";
import AnimatedNumbers from "react-animated-numbers";

const HomeSection4 = () => {
  const [num1, setNum1] = useState(20);
  const [num2, setNum2] = useState(30);
  const [num3, setNum3] = useState(4);

  return (
    <Row className="home-section-4-container" gutter={[20, 40]}>
      <Col xxl={8} xl={8} lg={8} md={24} sm={24} xs={24}>
        <div className="left">
          <div className="icon">
            <BsFillPersonFill />
          </div>
          <div className="value">
            {/* <div className="number">258+</div> */}
            <div className="number">
              {
                <AnimatedNumbers
                  animateToNumber={num1}
                  fontStyle={{ fontSize: 32 }}
                  configs={(number, index) => {
                    return {
                      mass: 1,
                      tension: 230 * (index + 1),
                      friction: 140,
                    };
                  }}
                ></AnimatedNumbers>
              }
              +
            </div>
            <div className="title">Happy Clients</div>
          </div>
        </div>
      </Col>
      <Col xxl={8} xl={8} lg={8} md={24} sm={24} xs={24}>
        <div className="middle">
          <div className="icon">
            <FiSettings />
          </div>
          <div className="value">
            {/* <div className="number">590K</div> */}
            <div className="number">
              {
                <AnimatedNumbers
                  animateToNumber={num2}
                  fontStyle={{ fontSize: 32 }}
                  configs={(number, index) => {
                    return {
                      mass: 1,
                      tension: 230 * (index + 1),
                      friction: 140,
                    };
                  }}
                ></AnimatedNumbers>
              }
              +
            </div>
            <div className="title">Project Complete</div>
          </div>
        </div>
      </Col>
      <Col xxl={8} xl={8} lg={8} md={24} sm={24} xs={24}>
        <div className="right">
          <div className="icon">
            <BsCalendar3 />
          </div>
          <div className="value">
            {/* <div className="number">28+</div> */}
            <div className="number">
              {
                <AnimatedNumbers
                  animateToNumber={num3}
                  fontStyle={{ fontSize: 32 }}
                  configs={(number, index) => {
                    return {
                      mass: 1,
                      tension: 230 * (index + 1),
                      friction: 140,
                    };
                  }}
                ></AnimatedNumbers>
              }
              +
            </div>
            <div className="title">Years of Experience</div>
          </div>
        </div>
      </Col>
    </Row>
  );
};

export default HomeSection4;
