import React from "react";
import { Row, Col } from "antd";

const HomeSection7 = ({comp}) => {
  return (
    <div className="home-section-7-container" style={comp==="ServiceDetail" ? {backgroundColor : '#282a37'}:{backgroundColor : '#2a2c39'}}>
      <div className="sec-1">
        <div className="top">
          <div className="line-container">
            <div className="line"></div>
            <div className="line"></div>
          </div>
          <div className="text-container">Favourite Clients</div>
        </div>
        <div className="title">Work With Trusted Comapny.</div>
      </div>
      <Row className="sec-2" gutter={[80, 40]} justify={"center"}>
        <Col xxl={6} xl={6} lg={8} md={12} sm={24} xs={24}>
          <div className="col-container">
            <div className="img-container">
              <img src="https://htmldemo.net/lendex/lendex/assets/images/company-logo/1.png" />
            </div>
          </div>
        </Col>
        <Col xxl={6} xl={6} lg={8} md={12} sm={24} xs={24}>
          <div className="col-container">
            <div className="img-container">
              <img src="https://htmldemo.net/lendex/lendex/assets/images/company-logo/2.png" />
            </div>
          </div>
        </Col>
        <Col xxl={6} xl={6} lg={8} md={0} sm={24} xs={24}>
          <div className="col-container">
            <div className="img-container">
              <img src="https://htmldemo.net/lendex/lendex/assets/images/company-logo/3.png" />
            </div>
          </div>
        </Col>
        <Col xxl={6} xl={6} lg={0} md={0} sm={24} xs={24}>
          <div className="col-container">
            <div className="img-container">
              <img src="https://htmldemo.net/lendex/lendex/assets/images/company-logo/4.png" />
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default HomeSection7;
