import React from "react";
import { BackTop, Col, Row } from "antd";
import { RiArrowUpSLine } from "react-icons/ri";
import Layout from "../layout/Layout";
import ProjectSection1 from "../components/projectssections/ProjectSection1";
import ProjectSection2 from "../components/projectssections/ProjectSection2";

const Projects = () => {
  return (
    <Layout>
      <div className="project-container">
        <Row className="sec-1">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}><ProjectSection1/></Col>
        </Row>
        <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <ProjectSection2/>
          </Col>
        </Row>
        <BackTop>
          <RiArrowUpSLine />
        </BackTop>
      </div>
    </Layout>
  );
};

export default Projects;
