import React from "react";
import { Row, Col } from "antd";
import ProgressComp from "../progress/ProgressComp";

const HomeSection3 = () => {
  const progressInfo = [
    { title: "Communication", percent: 75 },
    { title: "Leadership", percent: 78 },
    { title: "Teamwork", percent: 90 },
    { title: "Flexibility", percent: 80 },
  ];

  return (
    <Row className="home-section-3-container" gutter={[20, 60]}>
      <Col xxl={12} xl={12} lg={24} md={24} sm={24} xs={24}>
        <div className="left">
          <div className="top">
            <div className="line-container">
              <div className="line"></div>
              <div className="line"></div>
            </div>
            <div className="text-container">Special Skills</div>
          </div>
          <div className="middle">My Special Skill Field Here.</div>
          {/* <div className="btn-container">Scroll To Top</div> */}
        </div>
      </Col>
      <Col xxl={12} xl={12} lg={24} md={24} sm={24} xs={24}>
        <div className="right">
          {progressInfo?.map((item) => {
            return <ProgressComp title={item?.title} percent={item?.percent} />;
          })}
        </div>
      </Col>
    </Row>
  );
};

export default HomeSection3;
