import React, { useState } from "react";
import { Drawer, Collapse } from "antd";
import Logo from "../../assets/Logo.png";
import { Link } from "react-router-dom";
import { BsTwitter, BsSkype } from "react-icons/bs";
import { FaFacebookF } from "react-icons/fa";

const { Panel } = Collapse;

const MobileNav = () => {
  const [open, setOpen] = useState(false);
  const showDrawer = () => {
    setOpen(true);
  };
  const onClose = () => {
    setOpen(false);
  };
  return (
    <div className="mobile-nav-container">
      <div className="logo-container">
        Usama Ansari
      </div>
      <div className="ham-container" onClick={showDrawer}>
        <div className="line"></div>
        <div className="line"></div>
        <div className="line"></div>
      </div>
      <Drawer
        className="mobile-nav"
        placement="right"
        onClose={onClose}
        open={open}
      >
        <div className="drawer-container">
          <Link className="link-outer" to={"/"}>
            Home
          </Link>
          <Collapse accordion>
            <Panel header="SERVICES" key="1">
              <Link className="link" to={"/service"}>
                Service List
              </Link>
              <Link className="link" to={"/service-details"}>
                Service Details
              </Link>
            </Panel>
            <Panel header="Projects" key="2">
              <Link to={"/projects"} className="link">
                Projects
              </Link>
            </Panel>
          </Collapse>
          <Link className="link-outer" to={"/contact"}>
            CONTACT
          </Link>
        </div>
        <div className="link-container">
          {/* <div className="icon">
            <FaFacebookF />
          </div> */}
          <div className="icon">
            <BsTwitter />
          </div>
          <div className="icon">
            <BsSkype />
          </div>
        </div>
      </Drawer>
    </div>
  );
};

export default MobileNav;
