import React, { useEffect, useState } from "react";
import { Row, Col } from "antd";
import { ImStack } from "react-icons/im";
import { AiFillDatabase } from "react-icons/ai";
import { GiBrandyBottle } from "react-icons/gi";
import { FaHammer, FaPenFancy } from "react-icons/fa";
import Service from "../service card/Service";

const ServiceSection2 = () => {
  const serviceData = [
    {
      icon: <FaHammer />,
      title: "Web Development",
      subServices: [
        "HTML/CSS",
        "JavaScript",
        "Animation",
        "WordPress",
        "React",
      ],
    },
    {
      icon: <ImStack />,
      title: "Mobile App Development",
      subServices: [
        "React Native",
        "IOS",
        "Flutter",
        "Mobile Angular UI",
        "Mobile App Design",
      ],
    },
    {
      icon: <FaPenFancy />,
      title: "Illustration",
      subServices: [
        "Character Design",
        "Icon Set",
        "Illustration Guide",
        "Illustration Set",
        "Motion Graphic",
      ],
    },
    {
      icon: <ImStack />,
      title: "SEO Marketing",
      subServices: [
        "White Hat SEO",
        "Black Hat SEO",
        "Grey Hat SEO",
        "Negative SEO",
        "On-Page SEO",
      ],
    },
    {
      icon: <AiFillDatabase />,
      title: "Data Analysis",
      subServices: [
        " Diagnostic Analysis",
        "Predictive Analysis",
        "Prescriptive Analysis",
        "Risk Assessment",
        "Sales Forecasting",
      ],
    },
    {
      icon: <GiBrandyBottle />,
      title: "Brand Identity",
      subServices: [
        "Brand Architecture",
        "Brand Authenticity",
        "Brand Awareness",
        "Brand Management",
        "Brand Metrics",
      ],
    },
  ];

  const [data, setData] = useState();

  useEffect(() => {
    setData(serviceData);
  }, []);
  return (
    <div className="service-section-2-container">
      <Row className="service-section-2-wrapper" gutter={[30 , 30]}>
        {data?.map((item) => {
          return (
            <Col xxl={8} xl={12} lg={12} md={24} sm={24} xs={24}>
              <Service
                icon={item?.icon}
                title={item?.title}
                subServices={item?.subServices}
              />
            </Col>
          );
        })}
      </Row>
    </div>
  );
};

export default ServiceSection2;
