import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { Layout, Switch } from "antd";

import { TbBrandGithub } from "react-icons/tb";
import { BsInstagram } from "react-icons/bs";
import { FiTwitter, FiLinkedin } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";

// import WhiteLogo from "../assets/Logo.png";
// import BlueLogo from "../assets/blueLogo.png";
// import MainMenu from "./MainMenu";
import { toggleTheme } from "../redux";
import Navbar from "../components/navbar/Navbar";
import Footer from "../components/footer/Footer";

const VerticalLayout = ({ children, active }) => {
  console.log(children);
  const history = useHistory();
  const dispatch = useDispatch();
  const { Header, Content } = Layout;
  const theme = useSelector((state) => state.theme.theme);

  const [height, setHeight] = useState(null);

  const scrollWindow = () => {
    let scrollpos = window.scrollY;
    setHeight(scrollpos);
    console.log(height);
    console.log(scrollpos);
  };

  useEffect(() => {
    // window.addEventListener("scroll", () => {
    //   scrollpos = window.scrollY;
    //   console.log(scrollpos)
    // });
    window.addEventListener("scroll", scrollWindow);
    return () => {
      // window.removeEventListener("scroll", () => {
      //   scrollpos = window.scrollY;
      //   console.log(scrollpos)
      // });
      window.removeEventListener("scroll", scrollWindow);
    };
  }, []);

  return (
    <div className="v-layout">
      <Layout className="site-layout">
        <Header className="mainHeader">
          <Navbar />
        </Header>
        {/* <Switch
          className="themeSwitch"
          defaultChecked={theme === "light" ? false : true}
          checkedChildren={
            <MdDarkMode style={{ fontSize: "20px", marginRight: "5px" }} />
          }
          unCheckedChildren={
            <MdOutlineDarkMode
              style={{ fontSize: "20px", marginLeft: "5px" }}
            />
          }
          onChange={() => dispatch(toggleTheme())}
        /> */}
        <div className={height >= 690 ? "side-elements" : "side-elements-none"}>
          <div className="icon">
            <a target="_blank" href="https://github.com/engrusamaansari">
              <TbBrandGithub />
            </a>
          </div>
          {/* <div className="icon icon-one">
            <BsInstagram />
          </div> */}
          {/* <div className="icon">
            <FiTwitter />
          </div> */}
          <div className="icon">
            <a
              target="_blank"
              href="https://www.linkedin.com/in/m-usama-ansari/"
            >
              <FiLinkedin />
            </a>
          </div>
          <div className="line"></div>
        </div>
        <div
          className={
            height >= 690 ? "right-side-element" : "side-elements-none"
          }
        >
          <p className="text">engr.usamaansari@gmail.com</p>
          <div className="line"></div>
        </div>
        {children?.props?.className === "home-container" ? (
          <div className="icon">
            <div className="icon-style icon-1">
              <a target="_blank" href="https://github.com/engrusamaansari">
                <TbBrandGithub />
              </a>
            </div>
            {/* <div className="icon-style icon-style-one icon-2">
              <BsInstagram />
            </div> */}
            {/* <div className="icon-style icon-3">
              <FiTwitter />
            </div> */}
            <div className="icon-style icon-4">
              <a
                target="_blank"
                href="https://www.linkedin.com/in/m-usama-ansari/"
              >
                <FiLinkedin />
              </a>
            </div>
          </div>
        ) : null}
        <Content className="mainContent">{children}</Content>
        <Footer />
      </Layout>
    </div>
  );
};

export default VerticalLayout;
