import React from "react";
import { Row, Col, BackTop } from "antd";
import { RiArrowUpSLine } from "react-icons/ri";
import Layout from "../layout/Layout";
import ServiceDetailsSection1 from "../components/serviceDetailSections/ServiceDetailsSection1";
import HomeSection7 from "../components/homesections/HomeSection7";
import ServiceDetailsSection2 from "../components/serviceDetailSections/ServiceDetailsSection2";

const ServiceDetail = () => {
  return (
    <Layout>
      <div className="service-detail-container">
        <Row className="sec-1">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}><ServiceDetailsSection1/></Col>
        </Row>
        <Row className="sec-2">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}><ServiceDetailsSection2/></Col>
        </Row>
        <Row className="sec-3">
          <Col xxl={24} xl={24} lg={24} md={24} sm={24} xs={24}>
            <HomeSection7 comp={"ServiceDetail"}/>
          </Col>
        </Row>
        <BackTop>
          <RiArrowUpSLine />
        </BackTop>
      </div>
    </Layout>
  );
};

export default ServiceDetail;
